class Shape {

    constructor(x, y, radius) {
        this.center = { x, y };
        this.radius = radius;
    }

    addTransformY = value => {
        this.center.y += value;
    }
}

/*   *   *   *   *   *   *   *   *   *   */

class PlayerShape extends Shape {

    constructor(x, y, radius) {
        super(x, y, radius);
    }

    addTransformX = value => {
        this.center.x += value;
    }
}

export { PlayerShape, Shape };