import { PlayerShape, Shape } from './Shape';

/*   *   *   *   *   *   *   *   *   *   */

Math.approx = value => {
    return Math.round(1000 * value) / 1000;
};

Math.getRand = (min, max) => {
    return Math.floor(Math.random() * (max - min)) + min;
}

/*   *   *   *   *   *   *   *   *   *   */

class Game {

    constructor(selectorBoard, selectorPlayer) {

        this.selectorBoard = selectorBoard;
        this.selectorPlayer = selectorPlayer;
        this.result = null;

        //  Timing
        this.refreshInterval = null;
        this.spikeInterval = null;

        this.delay = null;
        this.def_delay = 2000;
        this.refreshRate = 100;

        //  State
        this.playerOffset = null;
        this.playerSide = false;

        //  Spikes
        this.spikes = [];
        this.score = 0;

        //  Seting up the game
        this.delay = this.def_delay;
        this.setUp();
    }

    setUp = () => {

        //  Basic elems
        this.board = document.querySelector(this.selectorBoard);
        this.player = document.querySelector(this.selectorPlayer);
        this.result = document.querySelector('.result');
        
        //  Player offset
        const playerX = (this.player.offsetLeft) + this.player.offsetWidth / 2;
        const playerY = (this.player.offsetTop) + this.player.offsetHeight / 2;
        this.playerOffset = new PlayerShape(playerX, playerY, this.player.offsetWidth / 2);
        
        this.start();
    }

    /*   *   *   *   *   *   *   *   *   *   */

    start = () => {

        this.result.textContent = this.score;

        this.initPlayerTransform();
        this.board.addEventListener('click', this.onClickAction);

        this.spikeInterval = setInterval(() => this.createNewSpike(), 500);
        this.refreshInterval = setInterval(() => this.transformSpikes(), 1000 / this.refreshRate);
    }

    stop = () => {
        this.board.removeEventListener('click', this.onClickAction);

        clearInterval(this.spikeInterval);
        clearInterval(this.refreshInterval);
    }
    
    initPlayerTransform = () => {
        const value = this.playerSide ? this.player.offsetLeft : -this.player.offsetLeft;
        const angle = this.playerSide ? 0 : -90;

        this.player.style.transform = `translateX(${value}px) rotate(${angle}deg)`;

        for(let i = 0; i < this.refreshRate; i++) {
            setTimeout(() => this.playerOffset.addTransformX(value /this.refreshRate), 1000 / this.refreshRate);
        }
    }

    onClickAction = () => {
        this.playerSide = !this.playerSide;

        const value = this.playerSide ? 2*this.player.offsetLeft : -2*this.player.offsetLeft;
        const angle = this.playerSide ? 0 : -90;

        this.transformPlayer(value, angle);
    }

    transformPlayer = (value, angle) => {

        this.player.style.transform = `translateX(${value/2}px) rotate(${angle}deg)`;

        for(let i = 0; i < this.refreshRate; i++) {
            setTimeout(() => this.playerOffset.addTransformX(value / this.refreshRate), 1000 / this.refreshRate);
        }
    }

    /*   *   *   *   *   *   *   *   *   *   */

    getRandomSide = () => {
        return Boolean(Math.floor(Math.random() *2)) ? '__left' : '__right';
    }

    createNewSpike = () => {
        const spike = document.createElement('div');

        spike.classList.add('spike', this.getRandomSide());
        spike.style.animationDuration = `${Math.approx(2*this.delay/(10*this.score + this.delay))}s`;

        this.addNewSpike(spike);
        this.removeSpike(spike);
    }

    transformSpikes = () => {
        const transform = window.innerHeight / (2*this.refreshRate);
        this.spikes.forEach(spike => spike.addTransformY(transform));

        this.collision();
    }

    addNewSpike = spike => {
        this.board.prepend(spike);

        const spikeX = (document.querySelector('.spike').offsetLeft) + document.querySelector('.spike').offsetWidth / 2;
        const spikeY = (document.querySelector('.spike').offsetTop) + document.querySelector('.spike').offsetHeight / 2;
        const radius = document.querySelector('.spike').offsetWidth / 2;
        const offset = new Shape(spikeX, spikeY, radius);

        this.spikes.unshift(offset)
    }

    removeSpike = spike => {
        setTimeout(() => {

            spike.remove();

            this.score ++;
            this.spikes.pop();
            this.result.textContent = this.score;
            this.delay = this.def_delay / ((this.score + this.def_delay)/this.def_delay);

        }, this.delay);
    }

    /*   *   *   *   *   *   *   *   *   *   */

    collision = () => {
        let isCollisoin = false;

        this.spikes.forEach(spike => {
            const distance = Math.sqrt((spike.center.x - this.playerOffset.center.x)**2 + (spike.center.y - this.playerOffset.center.y)**2);
            if(distance < spike.radius + this.playerOffset.radius) {isCollisoin = true};
        });
        

        if(isCollisoin) {
            console.log(this.spikes[this.spikes.length - 1]?.center);
            console.log(this.playerOffset.center);
            this.stop();
        }
    }
}

/*   *   *   *   *   *   *   *   *   *   */

export default Game;